.next-page{
  padding-top: calc(577 / 1440 * 100%);
  padding-left: 4.44%;
  position: relative;
  overflow: hidden;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
}
.next-page__inner{
  position: absolute;
  top: calc(246 / 577 * 100%);
  left: calc(64 / 1440 * 100%);
}
.next-page__inner_size_small{
  max-width: 365px;
}
.next-page__img{
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  z-index: -1;
}
.next-page__link{
  display: block;
  position: absolute;
  top: calc(142 / 577 * 100%);
  left: calc(491 / 1440 * 100%);
}